import React from 'react';

class Interactions extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            active: false
        }
    }

    render() {
        let {
            intImg,
            intImgH,
            linkOut,
            title
        } = this.props;

        return (
            <a className="intWrapper" href={linkOut} onMouseEnter={() => { this.setState({ active: true }) }} onMouseLeave={() => { this.setState({ active: false }) }}>
                <img src={this.state.active ? intImgH : intImg} alt={title} />
            </a>
        )
    }
}

export default Interactions;